<template><section><template>
<blockquote>
<p>Checkbox 多选框</p>
</blockquote>
  <br> 
<h5>一组备选项中进行多选</h5>
<br>
<br>
<br>
<h3><strong>基础用法</strong></h3>
<br>
<p>单独使用可以表示两种状态之间的切换，写在标签中的内容为 checkbox 按钮后的介绍。</p>
<br>
<br>
  <template>
    <ga-checkbox v-model="checked">多选框1
  </ga-checkbox></template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span>多选框1<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
      <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
          data(){
              <span class="hljs-keyword">return</span>{
                  <span class="hljs-attr">checked</span>:<span class="hljs-literal">true</span>
              }
          }
      }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h3><strong>禁用状态</strong></h3>
<br>
<p>多选框不可用状态。</p>
<br>
<br>
<template>
    <ga-checkbox class="ga-checkboxitem" v-model="checked2" disabled>多选框2</ga-checkbox>
    <ga-checkbox v-model="checked3">多选框3</ga-checkbox>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked2"</span> <span class="hljs-attr">disabled</span>&gt;</span>多选框2<span class="hljs-tag">&lt;/<span class="hljs-name">ga-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked3"</span>&gt;</span>多选框3<span class="hljs-tag">&lt;/<span class="hljs-name">ga-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
        <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
            data(){
                <span class="hljs-keyword">return</span>{
                    <span class="hljs-attr">checked2</span>:<span class="hljs-literal">false</span>,
                    <span class="hljs-attr">checked3</span>:<span class="hljs-literal">true</span>
                }
            }
        }
    </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<br>
<br>
<br>
<h3><strong>多选框组</strong></h3>
<br>
<p>适用于多个勾选框绑定到同一个数组的情景，通过是否勾选来表示这一组选项中选中的项。</p>
<br>
<br>
<template>
  <ga-checkbox-group @change="change" v-model="checkboxgrouplist">
    <ga-checkbox class="ga-checkboxitem" label="checkbox1"></ga-checkbox>
    <ga-checkbox class="ga-checkboxitem" label="checkbox2" disabled></ga-checkbox>
    <ga-checkbox class="ga-checkboxitem" label="checkbox3"></ga-checkbox>
  </ga-checkbox-group>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkboxgrouplist"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">ga-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"checkbox1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-checkbox</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">ga-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"checkbox2"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-checkbox</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">ga-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"checkbox3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">ga-checkbox</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">ga-checkbox-group</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
        <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
            data(){
                <span class="hljs-keyword">return</span>{
                    <span class="hljs-attr">checkboxgrouplist</span>:[<span class="hljs-string">'checkbox2'</span>,<span class="hljs-string">'checkbox3'</span>]
                }
            }
        }
    </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h1>Checkbox Attributes</h1>
  <br>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>Checkbox 的 value</td>
<td>Boolean</td>
<td>true / false</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>true / false</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>使用<code>ga-checkbox-group</code>时的<code>checkbox</code>的<code>label</code>值，单独使用<code>ga-checkbox</code>时<code>label</code>用插槽代替</td>
<td>String</td>
<td>medium / small / mini</td>
<td>—</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br>
<h1>Checkbox-group Events</h1>
  <br>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调函数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>绑定值变化时触发的事件</td>
<td>选中的 Checkbox label 值</td>
</tr>
</tbody>
</table>
</template>


</section></template>
<style lang="scss">
    .ga-checkboxitem{
        margin-right:12px;
    }
</style>
<script>
  export default {
    data () {
      return {
          radioData:'radio1',
          radioGroup:"radio-group1",
          radioGroup2:"禁用状态",
          checked:true,
          checked2:false,
          checked3:true,
          checkboxgrouplist:['checkbox2','checkbox3']
      }
    },
    methods:{
      change(val){
        console.log(val)
      }
    }
  }
</script>


